export default {
	data() {
		return {
			pwdType1: 'password',
			pwdType2: 'password'
		}
	},
	components: {

	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 切换input框类型
		lookPwd1(){
			if(this.pwdType1=='password'){
				this.pwdType1='text'
			}
			else if(this.pwdType1=='text'){
				this.pwdType1='password'
			}
		},
		lookPwd2(){
			if(this.pwdType2=='password'){
				this.pwdType2='text'
			}
			else if(this.pwdType2=='text'){
				this.pwdType2='password'
			}
		}
	},
}
